#search-header {
  background-color: #e9203d;
  color: #fff;
  padding: 10px;
  text-align: center;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
#search-header .back-icon {
  width: 16px;
  height: 16px;
  position: absolute;
  left: 10px;
}
#search-header .search-header-middle {
  width: 80%;
}
#search-header .search-header-middle .search-container-1 {
  background-color: #fff;
  border-radius: 15px;
  overflow: hidden;
  padding: 3px;
}
#search-header .search-header-middle .search-container-1 i {
  color: #ccc !important;
}



