#home-category {
  background-color: #fff;
  padding: 10px;
}
#home-category .carousel-item ul {
  width: 100%;
  height: auto;
}
#home-category .carousel-item li {
  display: block;
  list-style: none;
  width: 20%;
  font-size: 0;
  text-align: center;
  color: #666;
  font-size: 14px;
}
#home-category .carousel-item img {
  width: 100%;
  height: 90px;
}
#home-category .index-container {
  margin-top: 10px;
}
#home-category .index-container ul {
  width: 100%;
  height: auto;
  text-align: center;
}
#home-category .index-container li {
  list-style: none;
  display: inline-block;
  height: 8px;
  width: 8px;
  border-radius: 4px;
  background-color: #ccc;
  margin: 0 3px;
}
#home-category .index-container li.selected {
  background-color: #e9203d;
}



